// BORDERS
.b-a { border: $border; }
.b-b { border-bottom: $border; }
.b-l { border-left: $border; }
.b-r { border-right: $border; }
.b-t { border-top: $border; }
.b-x { border-left: $border; border-right: $border; }
.b-y { border-bottom: $border; border-top: $border; }


// BORDER-RADIUS
@for $i from 1 through 16 {
  .br-#{$i} { border-radius: $i * rem(1); }
}