// TYPOGRAPHY
body, button, input, select { color: $font-color; font-family: $font-family; font-size: $font-size; line-height: $font-line-height; }

h1 { font-size: $h1-font-size; font-weight: $h1-font-weight; }
h2 { font-size: $h2-font-size; font-weight: $h2-font-weight; }
h3 { font-size: $h3-font-size; font-weight: $h3-font-weight; }
h4 { font-size: $h4-font-size; font-weight: $h4-font-weight; }
h5 { font-size: $h5-font-size; font-weight: $h5-font-weight; }
h6 { font-size: $h6-font-size; font-weight: $h6-font-weight; }

a { color: $link-color; text-decoration: $link-text-decoration; &:focus, &:hover { color: $link-color-hover; text-decoration: $link-text-decoration-hover; } }
b, strong { font-weight: $font-weight-bold; }
em, i { font-style: italic; }
hr { border-top: $border; height: rem(1); }
small { font-size: $font-size-small; }


// CLASSES
.display-1 { font-size: $display-1-font-size; font-weight: $display-1-font-weight; }
.display-2 { font-size: $display-2-font-size; font-weight: $display-2-font-weight; }
.display-3 { font-size: $display-3-font-size; font-weight: $display-3-font-weight; }
.display-4 { font-size: $display-4-font-size; font-weight: $display-4-font-weight; }