// KEYFRAMES
@keyframes alert-animation-enter { from { opacity: 0; } to { opacity: 1; } }
@keyframes alert-animation-leave { from { opacity: 1; } to { opacity: 0; } }


// ALERTS
[class*=alert-] {
  border-radius: $alert-border-radius; padding: $alert-padding;
  &.au-enter-active { animation: alert-animation-enter .5s; }
  &.au-leave-active { animation: alert-animation-leave .5s; }
}

@each $name, $value in $colors {
  .alert-#{$name} { background: lighten($value, $lighten-percentage); border: solid rem(1) darken($value, $darken-percentage); color: $value; }
}