// IMPORTS
@import 'colors';


// VIEWPORTS
$xs-viewport: 0;
$sm-viewport: rem(544);
$md-viewport: rem(768);
$lg-viewport: rem(992);
$xl-viewport: rem(1200);
$xxl-viewport: rem(1920);


// GRID
$grid-breakpoints: (xs: $xs-viewport, sm: $sm-viewport, md: $md-viewport, lg: $lg-viewport, xl: $xl-viewport, xxl: $xxl-viewport);
$grid-columns: 12 !default;


// COLORS
$danger-color: map_get($flatui, 'alizarin') !default;
$info-color: map_get($flatui, 'sun-flower') !default;
$muted-color: map_get($flatui, 'concrete') !default;
$primary-color: map_get($flatui, 'peter-river') !default;
$success-color: map_get($flatui, 'emerald') !default;
$warn-color: map_get($flatui, 'carrot') !default;


// COLORS (LIST)
$brands: ('facebook': #3b5998, 'google': #dc4e41, 'linkedin': #0077b5);
$colors: ('danger': $danger-color, 'info': $info-color, 'primary': $primary-color, 'success': $success-color, 'warn': $warn-color);


// COLORS (PERCENTAGES)
$darken-percentage: 6%;
$lighten-percentage: 36%;


// BODY
$body-background: $white !default;
$body-background-attachment: fixed !default;
$body-background-size: cover !default;
$body-background-overlay: 0 !default;


// TYPOGRAPHY
$font-color: map_get($flatui, 'wet-asphalt') !default;
$font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif !default;
$font-line-height: 1.5 !default;
$font-size: rem(16, true);
$font-size-small: 85% !default;
$font-weight-bold: 700 !default;
$font-weight-normal: 400 !default;


// HEADINGS
$h1-font-size: rem(44, true);
$h1-font-weight: 400 !default;
$h2-font-size: rem(36, true);
$h2-font-weight: 400 !default;
$h3-font-size: rem(28, true);
$h3-font-weight: 400 !default;
$h4-font-size: rem(24, true);
$h4-font-weight: 400 !default;
$h5-font-size: rem(20, true);
$h5-font-weight: 400 !default;
$h6-font-size: rem(16, true);
$h6-font-weight: 400 !default;


// DISPLAYS
$display-1-font-size: rem(64, true);
$display-1-font-weight: 400 !default;
$display-2-font-size: rem(48, true);
$display-2-font-weight: 400 !default;
$display-3-font-size: rem(40, true);
$display-3-font-weight: 400 !default;
$display-4-font-size: rem(32, true);
$display-4-font-weight: 400 !default;


// LINK
$link-color: $success-color !default;
$link-color-hover: $success-color !default;
$link-text-decoration: none !default;
$link-text-decoration-hover: underline !default;


// ALERTS
$alert-border-radius: rem(4, true);
$alert-padding: rem(16, true);


// BORDERS
$border-color: map_get($flatui, 'clouds') !default;
$border: solid .0625rem $border-color !default;


// BUTTONS
$button-border-radius: rem(4, true);
$button-font-weight: 700 !default;
$button-padding: .5rem 1rem !default;


// INPUTS
$input-background: $white !default;
$input-border-color: map_get($flatui, 'silver') !default;
$input-border: solid .0625rem $input-border-color !default;
$input-border-radius: rem(4, true);
$input-padding: rem(8, true);


// LINE-BEHIND
$line-behind-background: $white !default;
$line-behind-padding: 0 .5rem !default;


// RIBBONS
$ribbon: false !default;
$ribbon-background: $warn-color !default;
$ribbon-border: solid .0625rem $white !default;
$ribbon-color: $white !default;
$ribbon-content: 'beta' !default;
$ribbon-right: -27px !default;
$ribbon-top: 20px !default;
$ribbon-width: rem(120, true);