// FONTS
.font-weight-bold { font-weight: $font-weight-bold; }
.font-weight-normal { font-weight: $font-weight-normal; }
.font-italic { font-style: italic; }


// TEXTS
.text-justify { text-align: justify; }

@each $size, $viewport in $grid-breakpoints {
  @include media-breakpoint-up($viewport) {
    .text-#{$size}-center { text-align: center; }
    .text-#{$size}-left { text-align: left; }
    .text-#{$size}-right { text-align: right; }
  }
}

.text-capitalize { text-transform: capitalize; }
.text-lowercase { text-transform: lowercase; }
.text-uppercase { text-transform: uppercase; }

.text-hide { color: transparent; text-indent: rem(-9999); }
.text-nowrap { white-space: nowrap; }
.text-truncate { overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }