// FORMS
input {
  background: $input-background; border: $input-border; border-radius: $input-border-radius;
  padding: $input-padding; transition: border .3s ease-out; width: 100%;
  &[type=checkbox], &[type=radio] { width: auto; }
  &:focus { border-color: $primary-color; }
}

.has-error {
  label, .error-message { color: $danger-color; }
  input {
    background-image: url('data:image/svg+xml;utf-8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 612 792"><path fill="#e74c3c" d="M447 544.4c-14.4 14.4-37.6 14.4-52 0l-89-92.7-89 92.7c-14.5 14.4-37.7 14.4-52 0-14.4-14.4-14.4-37.6 0-52l92.4-96.3-92.4-96.3c-14.4-14.4-14.4-37.6 0-52s37.6-14.3 52 0l89 92.8 89.2-92.7c14.4-14.4 37.6-14.4 52 0 14.3 14.4 14.3 37.6 0 52L354.6 396l92.4 96.4c14.4 14.4 14.4 37.6 0 52z"/></svg>');
    background-position: center right rem(4); background-repeat: no-repeat; background-size: rem(24) rem(24);
    border-color: $danger-color; padding-right: rem(32);
  }
}