// SPACING
.m-x-auto { margin-left: auto; margin-right: auto; }

@each $prop, $abbr in (margin: m, padding: p) {
  @for $i from 1 through 16 {
    $step: $i * 4; $length: $i * rem(4);
    .#{$abbr}-a-#{$step} { #{$prop}: $length; }
    .#{$abbr}-b-#{$step} { #{$prop}-bottom: $length; }
    .#{$abbr}-l-#{$step} { #{$prop}-left: $length; }
    .#{$abbr}-r-#{$step} { #{$prop}-right: $length; }
    .#{$abbr}-t-#{$step} { #{$prop}-top: $length; }
    .#{$abbr}-x-#{$step} { #{$prop}-left: $length; #{$prop}-right: $length; }
    .#{$abbr}-y-#{$step} { #{$prop}-bottom: $length; #{$prop}-top: $length; }
  }
}