// NORMALIZE - PART 1
* { background: 0; border: 0; display: block; font-weight: $font-weight-normal; margin: 0; outline: 0; padding: 0; }
a, code, small, span, strong { display: inline; }
button, input { display: inline-block; }
head, script, title { display: none; }


// NORMALIZE - PART 2
html { box-sizing: border-box; min-height: 100%; position: relative; }
*, *::before, *::after { box-sizing: inherit; }