// BODY
body {
  background: $body-background; background-attachment: $body-background-attachment; background-size: $body-background-size;
  height: 100%; text-rendering: optimizeLegibility; -webkit-font-smoothing: antialiased;
  @if $body-background-overlay != 0 {
    &::before { background: $body-background-overlay; bottom: 0; content: ''; display: block; left: 0; position: fixed; right: 0; top: 0; z-index: -1; }
  }
}