// SHADOWS
.shadow-1 { box-shadow: 0 rem(2) rem(5) 0 rgba(0,0,0,.16), 0 rem(2) rem(10) 0 rgba(0,0,0,.12); }
.shadow-2 { box-shadow: 0 rem(5) rem(11) 0 rgba(0,0,0,.18), 0 rem(4) rem(15) 0 rgba(0,0,0,.15); }
.shadow-3 { box-shadow: 0 rem(8) rem(17) 0 rgba(0,0,0,.2), 0 rem(6) rem(20) 0 rgba(0,0,0,.19); }
.shadow-4 { box-shadow: 0 rem(12) rem(15) 0 rgba(0,0,0,.24), 0 rem(17) rem(50) 0 rgba(0,0,0,.19); }
.shadow-5 { box-shadow: 0 rem(16) rem(28) 0 rgba(0,0,0,.22), 0 rem(25) rem(55) 0 rgba(0,0,0,.21); }
.shadow-6 { box-shadow: 0 rem(27) rem(24) 0 rgba(0,0,0,.2), 0 rem(40) rem(77) 0 rgba(0,0,0,.22); }


// TEXT SHADOWS
.text-shadow-1 { text-shadow: rem(2) rem(5) 0 rgba(0,0,0,.16); }