// FLEXBOX
.row { display: flex; flex: 0 1 auto; flex-direction: row; flex-wrap: wrap; }
.row.reverse { flex-direction: row-reverse; }
.col.reverse { flex-direction: column-reverse; }

@each $size, $viewport in $grid-breakpoints {
  @include media-breakpoint-up($viewport) {
    .col-#{$size} { flex: 1 0 0; max-width: 100%; }
    @for $i from 1 through $grid-columns {
      .col-#{$size}-#{$i} { flex: 0 0 percentage($i / $grid-columns); max-width: percentage($i / $grid-columns); }
      .col-#{$size}-offset-#{$i - 1} { margin-left: percentage(($i - 1) / $grid-columns); }
    }
    @each $halign, $valign in (left: top, center: middle, right:bottom) {
      $align: center;
      @if $halign == 'left' { $align: flex-start; }
      @if $halign == 'right' { $align: flex-end; }
      .#{$halign}-#{$size} { justify-content: $align; }
      .#{$valign}-#{$size} { align-items: $align; }
    }
    @each $justify in (around, between) {
      .#{$justify}-#{$size} { justify-content: space-#{$justify}; }
    }
    .first-#{$size} { order: -1; }
    .last-#{$size} { order: 1; }
  }
}