// MIXINS
@mixin media-breakpoint-down($viewport) {
  @if $viewport != 0 {
    @media (max-width: $viewport) {
      @content;
    }
  }
  @else {
    @content;
  }
}

@mixin media-breakpoint-up($viewport) {
  @if $viewport != 0 {
    @media (min-width: $viewport) {
      @content;
    }
  }
  @else {
    @content;
  }
}