// BUTTONS
[class*=btn-] {
  border-radius: $button-border-radius; cursor: pointer; font-weight: $button-font-weight; line-height: $font-line-height; margin-bottom: rem(3);
  padding: $button-padding; text-align: center; transition: background .3s ease-out, box-shadow .3s ease-out; word-wrap: break-word;
  &, &:focus, &:hover { text-decoration: none; } &:active { margin: rem(2) 0 rem(1); }
}

@each $list in $brands, $colors {
  @each $name, $value in $list {
    .btn-#{$name} {
      background: $value; box-shadow: 0 rem(3) 0 0 darken($value, $darken-percentage); color: $white;
      &:focus, &:hover { background: darken($value, $darken-percentage); box-shadow: 0 rem(3) 0 0 darken($value, 2 * $darken-percentage); color: $white; }
      &:active { box-shadow: 0 rem(1) 0 0 darken($value, $darken-percentage); }
    }
  }
}